<template>
    <div class="input-section">
        <label class="input-section__label" for="textarea">{{ labelText }}</label>
        <textarea 
            class="input-section__textarea" 
            id="textarea"
            :value="value"
            @input="$emit('input', $event.target.value)"></textarea>
    </div>
</template>

<script>
export default {
    name: 'Textarea',
    props: {
        labelText: {
            type: String,
        },
        value: {
            type: String,
        },
    },
}
</script>

<style>
.input-section__textarea {
    width: 100%;
    min-height: 88px;
    padding: 11px 7px;
    border: 1px solid var(--color-gray-light);
    border-radius: 5px;
    color: var(--color-gray-light);
}

.input-section__textarea:hover {
    border-color: var(--color-green-light);
}
</style>