<template>
    <div class="form-custom-section">

        <div class="form-custom-section__input-element">
            <InputField 
            class="reset-padding margin-right" 
            :inputId="inputId" 
            :labelText="labelText" 
            :inputType="inputType" 
            :errorMessage="errorMessage"
            :placeholder="placeholder" 
            v-model="inputText"/>

            <Button 
                class="margin-top-19"
                :buttonText="'Add'" 
                :padding="'6px 12px'" 
                :height="'50px'" 
                :icon="'/images/icon-add.svg'"
                @click.native.prevent="addToList"/>
        </div>

        <ul class="form-custom-section__list-elements">
            <ListItem 
                v-for="(item, index) in list" 
                :key="index" 
                :itemText="item" 
                :itemIndex="index"
                @removeItem="removeItem"/>
        </ul>

    </div>
</template>
<script>
import InputField from './InputField.vue';
import Button from './Button.vue';
import ListItem from './ListItem.vue';

export default {
    name: 'FormCustomSection',
    components: {
        InputField,
        Button,
        ListItem,
    },
    props: {
        inputType: {
            type: String,
            default: "Text",
        },
        inputId: {
            required: true,
            type: String,
        },
        labelText: {
            type: String,
        },
        placeholder: {
            type: String,
        },
        errorMessage: {
            type: String,
        },
    },
    data() {
        return {
            inputText: '',
            list: [],
        };
    },
    methods: {
        addToList() {
            if (!this.inputText) return;
            if (this.inputType === 'email') {
                const re = /^[\w-.]+@([\w-]+\.)+[\w-]{2,4}$/g;
                if (!re.test(this.inputText)) {
                    this.errorMessage = 'Введений текст не є коректним';
                    return;
                }
            }
            this.errorMessage = '';
            this.list.push(this.inputText);
            this.inputText = '';
        },
        removeItem(itemIndex) {
            this.list = this.list.filter((value, index) => index !== itemIndex);
        },
    },
    watch: {
        list() {
            this.$emit('changeData', this.inputType, this.list);
        }
    },
}
</script>

<style>
.form-custom-section {
    padding-bottom: 23px;
}

.form-custom-section__input-element {
    display: flex;
}

.form-custom-section__list-elements {
    list-style: none;
    padding: 0;
}

.margin-top-19 {
    margin-top: 19px;
}
</style>