<template>
    <button class="button" :class="classObject" :style="{ padding, width, height }">
        <img class="button__icon" v-if="icon" :src="icon" alt="button icon">
        {{ buttonText }}
    </button>
</template>

<script>
export default {
    name: 'Button',
    props: {
        buttonText: {
            type: String,
            default: '',
        },
        buttonType: {
            type: String,
            default: 'default',
        },
        padding: {
            type: String,
            default: 'auto',
        },
        width: {
            type: String,
            default: 'auto',
        },
        height: {
            type: String,
            default: 'auto',
        },
        icon: {
            type: String,
        },
    },
    computed: {
        classObject() {
            return {
                'button_default': this.buttonType === 'default',
                'button_outline': this.buttonType === 'outline',
                'button_flat': this.buttonType === 'flat',
            };
        },
    },
}
</script>

<style>
.button {
    border-radius: 5px;
    display: flex;
    align-items: center;
    justify-content: center;
    border: none;
    background: none;
    line-height: 19px;
    font-weight: 700;
}

.button:hover {
    cursor: pointer;
}

.button_default {
    background-color: var(--color-green-light);
    color: var(--color-white);
}

.button_default:hover {
    background-color: var(--color-green);
}

.button_outline {
    color: var(--color-green);
    border: 1px solid currentColor;
}

.button_flat {
    color: var(--color-green);
}

.button_outline:hover, .button_flat:hover {
    background-color: rgba(0, 211, 149, 0.15);;
}

.button__icon {
    width: 14px;
    height: 14px;
    margin-right: 12%;
}
</style>