<template>
    <div class="input-section">

        <label 
            class="input-section__label" 
            :for="inputId">{{ labelText }}</label>

        <input 
            class="input-section__input" 
            :class="{ 'input-section__input_error': errorMessage }" 
            :type="inputType" 
            :id="inputId" 
            :placeholder="placeholder"
            :value="inputText"
            @input="$emit('input', $event.target.value)">

        <small 
            v-if="errorMessage" 
            class="input-section__error-message">{{ errorMessage }}</small>

    </div>
</template>

<script>
export default {
    name: 'InputFiels',
    props: {
        inputText: {
            type: String,
        },
        inputType: {
            type: String,
            default: "Text",
        },
        inputId: {
            required: true,
            type: String,
        },
        labelText: {
            type: String,
        },
        placeholder: {
            type: String,
        },
        errorMessage: {
            type: String,
        },
    },
}
</script>

<style>
.input-section {
    width: 100%;
    padding-bottom: 23px;
}

.input-section__label {
    display: block;
    width: 100%;
    font-size: 12px;
    font-weight: 700;
    line-height: 14px;
    padding-left: 5px;
    padding-bottom: 5px;
}

.input-section__input {
    width: 100%;
    padding: 15px;
    border: 1px solid var(--color-gray-light);
    border-radius: 5px;
    display: block;
    font-size: 14px;
    line-height: 16px;
    color: var(--color-gray-light);
}

.input-section__input:hover {
    border-color: var(--color-green-light);
}

.input-section__input_error {
    border-color: var(--color-red);
}

.input-section__error-message {
    font-size: 12px;
    color: var(--color-red);
}
</style>