<template>
    <li class="list-item">
        <span class="list-item__text">
            {{ itemText }}
            <button 
                class="list-item__button"
                @click.prevent="removeItem(itemIndex)">
                <img class="list-item__button-icon" src="@/assets/icon-close-button.svg" alt="close icon">
            </button>
        </span>
    </li>
</template>

<script>
export default {
    name: 'ListItem',
    props: {
        itemText: {
            required: true,
            type: String,
        },
        itemIndex: {
            required: true,
            type: Number,
        },
    },
    methods: {
        removeItem(itemIndex) {
            this.$emit('removeItem', itemIndex);
        },
    },
}
</script>

<style>
.list-item {
    display: flex;
    align-items: center;
}

.list-item:not(:last-child) {
    padding-bottom: 8px;
}

.list-item__text {
    min-width: 160px;
    background-color: var(--color-gray);
    border: none;
    border-radius: 4px;
    font-size: 12px;
    line-height: 14px;
    color: #66707F;
    padding: 5px 11px 5px 23px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.list-item__button {
    border: none;
    background: none;
}

.list-item__button:hover {
    cursor: pointer;
}

.list-item__button-icon {
    width: 9px;
    height: 9px;
}
</style>