<template>
  <form 
    class="form"
    v-if="isFormOpen">

    <div>
      <legend class="form__header">Add new lead</legend>

      <button 
        class="form__button-close"
        @click.prevent="closeForm">

          <svg width="14" height="15" viewBox="0 0 14 15" fill="none" xmlns="http://www.w3.org/2000/svg">
            <path d="M12.892 0.965899C12.502 0.560999 11.872 0.560999 11.482 0.965899L6.59299 6.0324L1.703 0.9555C1.313 0.5506 0.682994 0.5506 0.291994 0.9555C-0.0970058 1.3604 -0.0970058 2.0145 0.291994 2.4194L5.18302 7.4963L0.291994 12.5733C-0.0970058 12.9782 -0.0970058 13.6322 0.291994 14.0371C0.682994 14.4421 1.313 14.4421 1.703 14.0371L6.59299 8.9602L11.482 14.0371C11.872 14.4421 12.502 14.4421 12.892 14.0371C13.282 13.6322 13.282 12.9782 12.892 12.5733L8.00202 7.4963L12.892 2.4194C13.272 2.0249 13.272 1.3604 12.892 0.965899Z" fill="#C2CFE0"/>
          </svg>
      </button>

      <InputField 
        :inputId="'userName'" 
        :labelText="'Enter your name'" 
        :placeholder="'https://goo.gl/maps/id'" 
        v-model="username"
        :errorMessage="errorMessage"/>

      <FormCustomSection 
        :inputId="'email'"
        :labelText="'Email'"
        :inputType="'email'"
        @changeData="changeData"/>

      <FormCustomSection 
        :inputId="'tel'"
        :labelText="'Phone'"
        :inputType="'tel'"
        @changeData="changeData"/>

      <InputField 
        :inputId="'Manager'" 
        :labelText="'Manager'"
        v-model="manager"/>

      <InputField 
        :inputId="'Status'" 
        :labelText="'Status'"
        v-model="status"/>

      <Textarea 
        :labelText="'Comment'"
        v-model="comments"/>
    </div>

    <Button 
      class="centered-gorizont" 
      :buttonText="'Save Lead'" 
      :padding="'10px 44px'" 
      @click.native.prevent="submitForm"/>

  </form>
</template>

<script>
import InputField from './InputField.vue';
import FormCustomSection from './FormCustomSection.vue';
import Textarea from './Textarea.vue';
import Button from './Button.vue';

export default {
  name: 'Form',
  components: {
    InputField,
    FormCustomSection,
    Textarea,
    Button,
  },
  data() {
    return {
      isFormOpen: true,
      username: '',
      emails: [],
      phones: [],
      manager: '',
      status: '',
      comments: '',
      errorMessage: '',
    };
  },
  methods: {
    closeForm() {
      this.isFormOpen = false;
    },
    changeData(inputType, list) {
      if (inputType === 'email') {
        return this.emails = list;
      }
      this.phones = list;
    },
    validateUserName() {
      if (this.username.length > 50) {
        this.errorMessage = 'Довжина імені занадто довга';
        return false;
      }
      if (this.username.length === 0) {
        this.errorMessage = 'Не вказано імя';
        return false;
      }
      return true;
    },
    validateEmail() {
      if (this.emails.length > 0) {
        return true;
      }
      return false;
    },
    submitForm() {
      const valid = this.validateUserName() && this.validateEmail();
      if (valid) {
        console.log('Поля форми валідні!');
      }
    },
  },
}
</script>

